<template>
  <div id="app">

    <b-row class="m-0">
      <b-col cols="8">
        <h2>Камера {{ id }}</h2>
      </b-col>
      <b-col class="float-right">
        <div v-if="chosenArea==-1">
          <div v-if="!isDrawing">
            <b-button v-on:click="isDrawing = 'true'" variant="secondary" class="mx-2">Создать область</b-button>
            <b-button v-if="arr.length" v-on:click="addController(arr)" class="mx-2" variant="secondary">Прикрепить
            </b-button>
          </div>
          <div v-else>
            <b-button v-if="i>2" v-on:click="closeArea" variant="dark" class="mx-2">Замкнуть область</b-button>
            <b-button v-on:click="delDrawingArea" variant="dark" class="mx-2">Отменить</b-button>
          </div>
        </div>
        <div v-else>
          <b-button v-on:click="deleteArea(chosenArea)" variant="dark" class="mx-2">Удалить выбранную область</b-button>
        </div>
      </b-col>
    </b-row>

    <div class="canvases">
      <canvas class="myCanvas" id="videoCanvas" style="z-index: 10"/>
      <canvas ref="canvas" id='drawing-pad' width='1280' height="640" style="z-index: 100; border: 2px solid black"
              class="myCanvas">
        This is an interactive drawing pad.
      </canvas>
    </div>

  </div>
</template>


<script>
import JSMpeg from 'jsmpeg-player'

export default {
  name: 'CameraPage',
  props: ['id'],
  data: () => ({
    points: [],
    area_id: 0,
    i: 0,
    arr: [],
    x: null, y: null,
    canvas: null,
    context: null,
    isDrawing: false,
    chosenArea: -1,
    rect: null
  }),
  mounted() {
    let canvas = document.getElementById('videoCanvas');
    let url = 'ws://192.168.1.35:5000';
    let player = new JSMpeg.Player(url, {
      canvas: canvas,
      autoplay: true, audio: false, loop: true,
      onVideoDecode: () => {
      }
    })
    this.canvas = this.$refs.canvas
    this.context = this.canvas.getContext("2d"); //ctx
    this.context.lineWidth = 2;
    this.context.strokeStyle = "blue"
    this.context.fillStyle = "rgba(0,255,255,0.4)"
    this.rect = this.canvas.getBoundingClientRect()
    this.canvas.addEventListener('click', this.click)
    // this.drawFromServer()
  },
  methods: {
    closeArea: function () {
      var area = {"id": this.area_id, "dots": this.points}
      this.area_id++;
      this.arr.push(area)
      // this.writeAreasToFile()
      this.points = []
      this.i = 0
      this.x = null
      this.y = null
      this.context.closePath()
      this.context.fill();
      this.context.stroke();
      this.isDrawing = false;
    },
    choseArea(id) {
      this.context.clearRect(0, 0, this.rect.width, this.rect.height)
      this.drawFromArray(this.arr)
      this.chosenArea = id
      var points = this.arr[id].dots
      this.context.beginPath();
      this.context.moveTo(points[0].x, points[0].y);
      for (var i = 0; i < points.length; i++) {
        this.context.lineTo(points[i].x, points[i].y)
      }
      this.context.closePath()
      this.context.strokeStyle = "red"
      this.context.fillStyle = "rgba(255,0,170, 0.4)"
      this.context.fill();
      this.context.stroke();
      this.context.strokeStyle = "blue"
      this.context.fillStyle = "rgba(0,255,255,0.4)"
    },
    deleteArea(id) {
      this.arr.splice(id, 1)
      this.context.clearRect(0, 0, this.rect.width, this.rect.height)
      this.drawFromArray(this.arr)
      this.chosenArea = -1
    },
    delDrawingArea() {
      this.points = []
      this.i = 0
      this.x = null
      this.y = null
      this.isDrawing = false

      this.context.clearRect(0, 0, this.rect.width, this.rect.height)
      this.drawFromArray(this.arr)
    },
    click(e) {
      var x = e.clientX - this.rect.left
      var y = e.clientY - this.rect.top
      if (!this.isDrawing) {
        if (this.inside({x, y}, this.arr) != -1) {
          this.choseArea(this.inside({x, y}, this.arr))
        }
      } else {
        this.points.push({x, y})
        if (this.i == 0) {
          this.context.beginPath();
          this.context.moveTo(x, y);
        }
        if (this.i > 0) {
          this.context.lineTo(x, y);
          this.context.stroke();
          if (Math.abs(x - this.points[0].x) < 10 && Math.abs(y - this.points[0].y) < 10) {
            alert('closeArea')
            this.points.pop()
            this.closeArea()
          }
        }
        this.i++
      }
    },
    drawFromArray(arr) {
      var points
      for (var i = 0; i < arr.length; i++) {
        points = arr[i].dots
        this.context.beginPath()
        this.context.moveTo(points[0].x, points[0].y);
        for (var j = 1; j < points.length; j++) {
          var x = points[j].x
          var y = points[j].y
          this.context.lineTo(x, y);
        }
        this.context.closePath()
        this.context.fill();
        this.context.stroke();
      }
    },
    async addController(arr) {
      this.$router.push({name: 'addController', params: {arr: this.arrPxToProc(arr), id: this.id}})
    },
    pxToProcent(x, y) {
      ;
      return {'x': Math.round((x / this.rect.width) * 100), 'y': Math.round((y / this.rect.height) * 100)}
    },
    procentToPx(x, y) {
      ;
      return {'x': x * this.rect.width / 100, 'y': y * this.rect.height / 100}
    },
    inside(point, vs) {
      var x = point.x, y = point.y;
      var inside = false, points = []
      for (var k = 0; k < vs.length; k++) {
        points = vs[k].dots
        for (var i = 0, j = points.length - 1; i < points.length; j = i++) {
          var xi = points[i].x, yi = points[i].y;
          var xj = points[j].x, yj = points[j].y;

          var intersect = ((yi > y) != (yj > y))
              && (x < (xj - xi) * (y - yi) / (yj - yi) + xi);
          if (intersect) inside = !inside;
        }
        if (inside) {
          return k
        }
      }
      return -1
    },
    arrPxToProc(arr) {
      console.log(arr.length)
      var points, arr1 = [], points2 = [], dot
      for (var i = 0; i < arr.length; i++) {
        points = arr[i].dots
        console.log(points, points.length)
        for (var j = 0; j < points.length; j++) {
          dot = this.pxToProcent(points[j].x, points[j].y)
          points2.push(dot)
        }
        arr1.push({'id': i, 'dots': points2})
        points = null
      }
      return arr1
    },
    right_click(e) {
      e.preventDefault();
      ;
      var x = e.clientX - this.rect.left
      var y = e.clientY - this.rect.top;
      var k = this.inside({x, y}, this.arr)
      console.log(k)
      // if (this.context.isPointInPath(x,y)) {
      //   this.arr.pop()
      //   this.context.clearRect(0,0,rect.width,rect.height)
      // }
      // this.drawFromArray(this.arr)
    },
  }
}
</script>

<style>
body {
  margin: 0;
}

.canvases {
  position: relative;
}

.myCanvas {
  position: absolute;
  width: 1280px;
  height: 640px;
  top: 0px
}
</style>